  filter: blur(50px);
  mix-blend-mode: hard-light;
}

.gradient-blur-4 {
  background: radial-gradient(ellipse at center, rgba(100, 200, 255, 0.3) 0%, transparent 70%);
  filter: blur(70px);
}

/* Section headings */
.section-heading {
  font-family: var(--font-noto-serif), serif;
  font-weight: 500;
  font-size: 80px;
  line-height: 1;
  color: #1c2122;
}

@media (max-width: 1024px) {
  .section-heading {
    font-size: 56px;
  }
}

@media (max-width: 640px) {
  .section-heading {
    font-size: 40px;
  }
}

.hover\:scale-110:hover {
  --tw-scale-x: 1.1;
  --tw-scale-y: 1.1;
  transform: translate(var(--tw-translate-x), var(--tw-translate-y)) rotate(var(--tw-rotate)) skewX(var(--tw-skew-x)) skewY(var(--tw-skew-y)) scaleX(var(--tw-scale-x)) scaleY(var(--tw-scale-y));
}

.hover\:text-white:hover {
  --tw-text-opacity: 1;
  color: rgb(255 255 255 / var(--tw-text-opacity, 1));
}

.group:hover .group-hover\:text-text-dark-alt {
  --tw-text-opacity: 1;
  color: rgb(26 27 28 / var(--tw-text-opacity, 1));
}

@media (min-width: 640px) {

  .sm\:h-\[400px\] {
    height: 400px;
  }
}

@media (min-width: 768px) {

  .md\:grid-cols-3 {
    grid-template-columns: repeat(3, minmax(0, 1fr));
  }
}

